@import url(https://fonts.googleapis.com/css?family=Open+Sans:600|Work+Sans:400,500,600,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{font-family:"Montserrat", sans-serif;font-style:normal;line-height:1.4;font-size:100%;text-align:center;background:#22c1c3;background:linear-gradient(135deg, #22c1c3 0%, #fdbb2d 100%);font-family:"work sans"}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background:#5a36e8;min-height:100vh;display:flex;flex-direction:column;align-items:center;color:#fff;justify-content:center;font-size:calc(10px + 2vmin);font-family:Georgia, 'Times New Roman', Times, serif;text-shadow:#000}.Chart{background:#5a36e8;min-height:100vh;display:flex;flex-direction:column;align-items:center;width:100%}.App-link{color:#61dafb}.h2,h2{padding:1.7rem}th,td{color:green}table.table{color:green;border:none;justify-self:start}table.table td,table.table th{color:green;border-bottom:1px solid #056304;padding:15px 19px}table.table td{color:green;padding:11px 15px}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.button:hover{background:#056304}label{color:#000;line-height:1.5;margin-bottom:0}Input[type=text]{border:1px solid}#loginform{max-width:500px;min-width:300px;max-height:700px;width:30%;height:60%;margin:100px auto;background-color:#FFFFFF;border-radius:25px}#headerTitle{text-align:center;font-family:'open sans', sans-serif;padding:2rem 0;margin:0;font-size:2rem}.row{display:flex;flex-direction:column;align-items:center;padding-top:2rem;max-width:100%}.row input{width:80%;box-sizing:border-box;border:none;font-size:1.3rem;padding-left:1.5rem;padding-bottom:1rem;box-shadow:inset 0px -3px 0px 0px rgba(187,187,187,0.2);transition:box-shadow 0.2s ease-in}.row input:focus{box-shadow:inset 0px -3px 0px 0px rgba(34,193,195,0.7);outline:none}.row input::-webkit-input-placeholder{opacity:1;-webkit-transition:opacity 0.25s ease-out;transition:opacity 0.25s ease-out}.row input:hover::-webkit-input-placeholder,.row input:focus::-webkit-input-placeholder{opacity:0}.row label{align-self:start;padding-left:4.5rem;padding-bottom:0.5rem;color:rgba(187,187,187,0.9)}.row button{margin-left:26px;border-radius:25px;width:80%;height:40px;font-size:1.3rem;color:white;font-weight:700;background:#22c1c3;background:linear-gradient(90deg, rgba(0,220,255,0.878431) 0%, rgba(0,195,204,0.780392) 100%);border:0px;cursor:pointer;transition:opacity 0.25s ease-out}.row button:hover{opacity:0.8}#button{padding-bottom:1.5rem}#facebookIcon{background-image:url("https://image.flaticon.com/icons/svg/145/145802.svg");background-repeat:no-repeat;width:3rem;height:3rem}#twitterIcon{background-image:url("https://image.flaticon.com/icons/svg/145/145812.svg");background-repeat:no-repeat;width:3rem;height:3rem}#googleIcon{background-image:url("https://image.flaticon.com/icons/svg/145/145804.svg");background-repeat:no-repeat;width:3rem;height:3rem}#iconGroup{display:flex;justify-content:center;padding:2rem 0}#iconGroup a{margin:0 0.6rem}#alternativeLogin{text-align:center;padding-top:2rem;margin-top:1.5rem}

